body {
  margin: 10px;
  background: #10191f;
  -moz-transform: scale(0.6, 0.6); /* Moz-browsers */
  zoom: 0.6; /* Other non-webkit browsers */
  zoom: 60%; /* Webkit browsers */
}

.photo-hanger {
  justify-content: center;
  max-width: 1150px;

  .greetings {
    margin-top: 10px;

    button {
      outline: 0;
      background-color: #666;
      border-radius: 5px;
      color: white;
      border: 0;
      padding: 10px;
      font-weight: bold;
      cursor: pointer;
      text-transform: uppercase;
      transition: 0.25s all;
      
      &:hover {
          background-color: #F07818;
      }
    }
  }
}

.tilt-right {
  -ms-transform: rotate(10deg);
  -webkit-transform: rotate(10deg);
  transform: rotate(10deg);

  &.extra {
    -ms-transform: rotate(20deg);
    -webkit-transform: rotate(20deg);
    transform: rotate(20deg);
  }

  &.slight {
    -ms-transform: rotate(5deg);
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
  }
}

.tilt-left {
  -ms-transform: rotate(-10deg);
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);

  &.extra {
    -ms-transform: rotate(-20deg);
    -webkit-transform: rotate(-20deg);
    transform: rotate(-20deg);
  }

  &.slight {
    -ms-transform: rotate(-5deg);
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
}

.photo-card-wrapper {
  padding: 0px;
  max-width: 200px;
  margin-bottom: 100px;

  .clip {
    max-width: 100%;
    z-index: 1000;
    position: relative;
  }

  .photo-card {
    display: flex;
    flex-direction: column;
    box-shadow: 14px 15px 8px 0 rgba(255, 255, 255, 0.2);
    transition: 0.3s;
    border-radius: 4px;
    max-width: 400px;
    height: 250px;
    margin: 5px;
    padding: 10px;
    background: #e7e7e7;
    margin-top: -10px;
  
    &:hover {
      box-shadow: 14px 30px 16px 0 rgba(255,255,255,0.2);
    }
  
    .photo {
      width: 100%;
      flex: 1;
      overflow: hidden;

      img {
        width: 100%;
      }
    }
  
    .greetings {
      width: 100%;
      height: 50px;
    }
  }
}

.alphabet-container {
  display: flex;
  top: 0;
  justify-content: center;
  margin: 0 50px;

  &.right {
    right: 0;
  }

  &.left {
    left: 0;
  }

  .alphabet {
    &.vertical {
      height: 60px;
        &.small {
          height: 30px;
        }
    }
  
    &.horizontal {
      width: 30px;
      height: 5px;

      &.small {
        width: 15px;
        height: 3px;
      }
    }
  }
  
  .flex {
    display: flex;
    align-items: center;
  
    &.flex-row {
      flex-direction: row;
    }
  
    &.flex-col {
      flex-direction: column;
    }
  }
}